import React from "react";
import { AiFillGithub } from "react-icons/ai";
import { CgWebsite } from "react-icons/cg";
import ReactTooltip from "react-tooltip";
import "./ProjectCard.css";

function ProjectCard({ image, title, description, gitlink, weblink }) {
  return (
    <div
      className="ProjectCard"
      style={{
        backgroundImage: `url(${image})`,
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center",
        backgroundSize: "cover",
      }}
    >
      <div className="ProjectCard__overlay">
        <div className="ProjectCard__title">{title}</div>
        <p className="ProjectCard__description">{description}</p>
        <div className="ProjectCard__logos">
          <AiFillGithub
            data-tip
            data-for="github"
            className="ProjectCard__gitlogo"
            onClick={() => window.open(gitlink, "_blank")}
            size="40%"
          />
          <ReactTooltip id="github" type="error">
            <span>GitHub</span>
          </ReactTooltip>
          <CgWebsite
            data-tip
            data-for="livewebsite"
            onClick={() => window.open(weblink, "_blank")}
            className="ProjectCard__webIcon"
          />
          <ReactTooltip id="livewebsite" type="error">
            <span>Live Website</span>
          </ReactTooltip>
        </div>
      </div>
    </div>
  );
}

export default ProjectCard;

