import React, { useState, useEffect } from "react";
import profilepic from "../images/profilepic.jpg";
import { FaReact } from "react-icons/fa";
import { SiJavascript } from "react-icons/si";
import { FaNodeJs } from "react-icons/fa";
import { FaVuejs } from "react-icons/fa";
import { SiRedux } from "react-icons/si";
import { SiNextDotJs } from "react-icons/si";
import { SiFirebase } from "react-icons/si";
import { SiMongodb } from "react-icons/si";
import { FaPython } from "react-icons/fa";
import ReactTooltip from "react-tooltip";
import "./Intro.css";

function Intro() {
  const [offsetY, setOffsetY] = useState(0);
  const [isActive, setActive] = useState("false");

  const handleScroll = () => setOffsetY(window.pageYOffset);

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);
  return (
    <div className="Intro">
      <div class="Intro__card">
        <img
          className="Intro__profilepic"
          src={profilepic}
          style={{ backgroundSize: "cover" }}
          alt=""
        />
        <h1>Nischal Tripathi</h1>
        <p className="Intro__subheading">MERN Stack Developer</p>
        <span className="Intro__about">
          I'm a final year Computer Science student at The University of
          Adelaide. I'm persuing Cyber Security as my major. I'm a MERN stack
          developer. I also have great coding experince in programming languages
          like Javascript, C/C++, Python, etc. I'm proficient with Object
          Oriented Programming, Object Oriented Programming, Algorithm and Data
          Structures, and Algorithm Design and Data Structures.
        </span>
        <span className="Intro__Skills">
          <p className="Intro__skill">My Skills:</p>
          <FaReact data-tip data-for="react" className="Intro__reactLogo" />
          <ReactTooltip id="react" type="error">
            <span>ReactJS</span>
          </ReactTooltip>
          <SiJavascript
            data-tip
            data-for="javascript"
            className="Intro__jsLogo"
          />
          <ReactTooltip id="javascript" type="error">
            <span>Javascript</span>
          </ReactTooltip>
          <FaNodeJs data-tip data-for="nodejs" className="Intro__nodeLogo" />
          <ReactTooltip id="nodejs" type="error">
            <span>NodeJS</span>
          </ReactTooltip>
          <FaVuejs data-tip data-for="vuejs" className="Intro__vueLogo" />
          <ReactTooltip id="vuejs" type="error">
            <span>VueJS</span>
          </ReactTooltip>
          <SiRedux data-tip data-for="redux" className="Intro__reduxLogo" />
          <ReactTooltip id="redux" type="error">
            <span>Redux</span>
          </ReactTooltip>
          <SiNextDotJs data-tip data-for="nextjs" className="Intro__nextLogo" />
          <ReactTooltip id="nextjs" type="error">
            <span>NextJS</span>
          </ReactTooltip>
          <SiFirebase
            data-tip
            data-for="firebase"
            className="Intro__firebaseLogo"
          />
          <ReactTooltip id="firebase" type="error">
            <span>Firebase</span>
          </ReactTooltip>
          <SiMongodb data-tip data-for="mongodb" className="Intro__mongoLogo" />
          <ReactTooltip id="mongodb" type="error">
            <span>MongoDB</span>
          </ReactTooltip>
          <FaPython data-tip data-for="python" className="Intro__pythonLogo" />
          <ReactTooltip id="python" type="error">
            <span>Python</span>
          </ReactTooltip>
        </span>
      </div>
    </div>
  );
}

export default Intro;
